#app {
  display: grid;
  grid-template-areas: "header header" "aside main" "aside footer";
  grid-template-columns: minmax(200px, 264px) auto;
  grid-template-rows: auto auto auto;
  overflow: hidden;
  max-width: 1920px;
  margin: 0 auto;
}
body,
html {
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  color: #060028;
}
.h1,
.h2,
.h3,
.h4 h1,
h2,
h3,
h4 {
  font-weight: 600;
}
.h1,
h1 {
  font-size: 60px;
}
.h3,
h3 {
  font-size: 44px;
}
.btn-primary {
  color: #FFFFFF;
  font-weight: 500;
  letter-spacing: 0.015em;
  line-height: 125%;
  background: #EB00FF;
  border: 1px solid #EB00FF;
  padding: 12px 27px;
  border-radius: 0.25rem;
  border-radius: 8px;
  transition: all 0.4s ease-in;
}
.btn-primary:hover,
.btn-primary:focus,
.btn-primary:active {
  color: #FFFFFF;
  background: #F691FF;
  border: 1px solid #F691FF;
}
.btn-dark {
  font-weight: 500;
  line-height: 125%;
  letter-spacing: 0.015em;
  background: #2F0DFF;
  padding: 12px 27px;
  border-radius: 8px;
  border: 1px solid transparent;
  transition: all 0.4s ease-in;
}
.btn-dark:hover,
.btn-dark:focus,
.btn-dark:active {
  background: #2d0dff44;
  border: 1px solid transparent;
}
.btn-warning {
  line-height: 125%;
  letter-spacing: 0.015em;
}
.btn-outline-danger {
  color: #EB00FF;
  font-weight: 500;
  line-height: 106.8%;
  letter-spacing: 0.015em;
  padding: 12px 27px;
  border: 2px solid #EB00FF;
  transition: all 0.4s ease-in;
  background-color: transparent;
}
.btn-outline-danger:hover,
.btn-outline-danger:active,
.btn-outline-danger:focus {
  color: #fff;
  background-color: #EB00FF;
  border-color: #EB00FF;
}
.btn-warning {
  padding: 12px 27px;
}
::-webkit-scrollbar-thumb {
  width: 8px;
  height: 26px;
  background: #000;
  border-radius: 10px;
  background-color: #060028;
}
::-webkit-scrollbar {
  width: 10px;
  background-color: #f9f9fd8a;
}
::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #9d92d71d;
}
.main {
  grid-area: main;
  min-height: 100vh;
  position: relative;
}
.form-label {
  font-size: 0;
}
.br {
  border-right: 1px solid #fff;
}
@media (max-width: 992px) {
  #app {
    grid-template-areas: "header" "main" "footer";
    grid-template-columns: 100%;
    grid-template-rows: auto auto auto;
    position: relative;
  }
  .aside {
    position: absolute;
    top: 74px;
    left: 0;
    padding: 10px 20px;
    z-index: 35;
    transform: translateX(-100%);
    transition: all 0.6s ease-in;
    background: #fff;
  }
  .naw-show {
    transform: translateX(0);
  }
}
.toast-btn {
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  opacity: 0.5;
  background-color: transparent;
  border: 0;
}
.toast-text {
  width: 246px;
}
@media (max-width: 368px) {
  .toast-text {
    width: 230px;
  }
}
.logo-link {
  color: #fff;
  text-decoration: none;
  transition: all 0.3s ease-out;
}
.logo-link:hover {
  color: #ffffff58;
  text-decoration: none;
}
.card {
  border: 1px solid rgba(235, 0, 255, 0.75);
}
.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
  color: #150E41;
  background-color: #fff;
  border-color: rgba(235, 0, 255, 0.75) rgba(235, 0, 255, 0.75) #fff;
  border-bottom: 2px solid #fff;
}
.table td,
.table th {
  border-top: 1px solid rgba(235, 0, 255, 0.75);
}
.tab-content .tab-pane .table thead th {
  border-bottom: 1px solid rgba(235, 0, 255, 0.75);
}
.tab-content .table thead th {
  border-top: none;
}
.tab-content tr:first-child th,
.tab-content tr:first-child td {
  border-top: none;
}
.nav-tabs .nav-item {
  margin-bottom: -2px;
}
.modal-content .table thead th {
  border-bottom: 1px solid transparent;
}
.nav-tabs {
  border-bottom: 1px solid transparent;
}
.nav-tabs:last-child {
  padding-right: 12px;
}
.table-wrap {
  border: 1px solid rgba(235, 0, 255, 0.75);
  border-top-right-radius: 12px;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 20px 20px;
}
.nav-tabs .nav-link {
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}
@media (max-width: 362px) {
  .table-wrap {
    overflow-x: scroll;
  }
}
.form-control {
  font-size: 1.2 rem;
}
